export const onOnboardingAvailabilityCreated = EventDefinition(
  'wix.cashier.onboarding_availability.v1.onboarding_availability_created',
  true,
  (event: OnboardingAvailabilityCreatedEnvelope) =>
    renameKeysFromRESTResponseToSDKResponse(
      transformPaths(event, [
        {
          transformFn: transformRESTTimestampToSDKTimestamp,
          paths: [
            {
              path: 'entity.cbdFlow.attestationInfo.attestationFormSignedDate',
            },
            { path: 'metadata.eventTime' },
          ],
        },
      ])
    )
)<OnboardingAvailabilityCreatedEnvelope>();
